.total {
  background-color: black;
  height: 100vh;
  width: 100%;
  padding: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 50px;
  padding-top: 40px;
  align-items: center;
  width: 500px;
  height: 300px;
  background-color: greenyellow;
  border-radius: 20px;
  border-style: ridge;
}
input {
  height: 50px;
  width: 100%;
  border-radius: 10px;
  margin: 10px;
  margin-left: -50px;
}
.btn-hi {
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  height: 40px;
  width: 80px;
  background-color: black;
  color: greenyellow;
  border-radius: 9px;
  border: none;
  margin-left: 20px;
}
h1 {
  color: greenyellow;
  font-size: 50px;
  text-transform: uppercase;
  font-weight: bold;
}
.center {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.textstl {
  font-weight: bold;
  margin-left: -45px;
  font-size: 1.2rem;
  margin-top: 0px;
}
@media screen and (max-width: 756px) {
  h1 {
    color: greenyellow;
    font-size: 40px;
    text-transform: uppercase;
    font-weight: bold;
  }
  .btn-hi {
    font-size: 17px;
    margin-left: -50px;
    align-items: center;
  }
  .center {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .body {
    width: 400px;
    align-items: center;
  }
  input {
    margin-left: -50px;
  }
}
